.ng2-o-box {
  background-color: #ffffff;
  border-radius: 4px;
  padding: 5px 15px; }

.ng2-o-box__header {
  margin-bottom: 20px; }

.ng2-o-box__title {
  color: #28648a;
  font-size: 24px;
  font-weight: 400;
  line-height: initial;
  margin: 0; }

.ng2-o-box--fluid {
  display: flex;
  flex: 1;
  flex-direction: column;
  margin: 0 10px; }
  @media (min-width: 414px) {
    .ng2-o-box--fluid {
      flex: 0 1 auto;
      margin: 0 auto;
      max-width: 490px;
      position: relative;
      width: calc(100% - 50px); } }
  .ng2-o-box--fluid .ng2-o-box__main {
    max-width: 360px;
    overflow: auto;
    -ms-overflow-style: none; }
    @media (min-width: 414px) {
      .ng2-o-box--fluid .ng2-o-box__main {
        margin: 0 auto; } }

.ng2-o-box--scrollable {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  max-height: calc(100vh - 80px);
  overflow: auto; }
  .ng2-o-box--scrollable .ng2-o-box__header,
  .ng2-o-box--scrollable .ng2-o-box__footer {
    flex-shrink: 0; }
  .ng2-o-box--scrollable .ng2-o-box__main {
    flex-shrink: 1; }

.body--is-ios .ng2-o-box--scrollable {
  max-height: calc(100vh - 80px - 20px);
  max-height: calc(100vh - 80px - constant(safe-area-inset-top));
  max-height: calc(100vh - 80px - env(safe-area-inset-top)); }

.ng2-o-box--transparent {
  background: none;
  border-radius: 0;
  box-shadow: none; }
  .ng2-o-box--transparent .ng2-o-box__title {
    color: #ffffff; }
  .ng2-o-box--transparent .ng2-o-box__icon {
    color: rgba(255, 255, 255, 0.5); }

.ng2-o-box--shadow {
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2); }

.ng2-o-box--padded {
  padding: 15px; }
